import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Row, Col } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { ICrudGetAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntity } from './ps-custom-fields-values.reducer';
import { IPSCustomFieldsValues } from 'app/shared/model/ps-custom-fields-values.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';

export interface IPSCustomFieldsValuesDetailProps extends StateProps, DispatchProps, RouteComponentProps<{ id: number }> {}

export class PSCustomFieldsValuesDetail extends React.Component<IPSCustomFieldsValuesDetailProps> {
  componentDidMount() {
    this.props.getEntity(this.props.match.params.id);
  }

  render() {
    const { pSCustomFieldsValuesEntity } = this.props;
    return (
      <Row>
        <Col md="8">
          <h2>
            PSCustomFieldsValues [<b>{pSCustomFieldsValuesEntity.id}</b>]
          </h2>
          <dl className="jh-entity-details">
            <dt>
              <span id="value">Value</span>
            </dt>
            <dd>{pSCustomFieldsValuesEntity.value}</dd>
            <dt>Psentity</dt>
            <dd>{pSCustomFieldsValuesEntity.psentity ? pSCustomFieldsValuesEntity.psentity.id : ''}</dd>
            <dt>Pscustomfieldstemplate</dt>
            <dd>{pSCustomFieldsValuesEntity.pscustomfieldstemplate ? pSCustomFieldsValuesEntity.pscustomfieldstemplate.id : ''}</dd>
          </dl>
          <Button tag={Link} to="/entity/ps-custom-fields-values" replace color="info">
            <FontAwesomeIcon icon="arrow-left" /> <span className="d-none d-md-inline">Back</span>
          </Button>&nbsp;
          <Button tag={Link} to={`/entity/ps-custom-fields-values/${pSCustomFieldsValuesEntity.id}/edit`} replace color="primary">
            <FontAwesomeIcon icon="pencil-alt" /> <span className="d-none d-md-inline">Edit</span>
          </Button>
        </Col>
      </Row>
    );
  }
}

const mapStateToProps = ({ pSCustomFieldsValues }: IRootState) => ({
  pSCustomFieldsValuesEntity: pSCustomFieldsValues.entity
});

const mapDispatchToProps = { getEntity };

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PSCustomFieldsValuesDetail);
