import axios from 'axios';
import { ICrudGetAction, ICrudGetAllAction, ICrudPutAction, ICrudDeleteAction } from 'react-jhipster';

import { cleanEntity } from 'app/shared/util/entity-utils';
import { REQUEST, SUCCESS, FAILURE } from 'app/shared/reducers/action-type.util';

import { IPSCustomFieldsValues, defaultValue } from 'app/shared/model/ps-custom-fields-values.model';

export const ACTION_TYPES = {
  FETCH_PSCUSTOMFIELDSVALUES_LIST: 'pSCustomFieldsValues/FETCH_PSCUSTOMFIELDSVALUES_LIST',
  FETCH_PSCUSTOMFIELDSVALUES: 'pSCustomFieldsValues/FETCH_PSCUSTOMFIELDSVALUES',
  CREATE_PSCUSTOMFIELDSVALUES: 'pSCustomFieldsValues/CREATE_PSCUSTOMFIELDSVALUES',
  UPDATE_PSCUSTOMFIELDSVALUES: 'pSCustomFieldsValues/UPDATE_PSCUSTOMFIELDSVALUES',
  DELETE_PSCUSTOMFIELDSVALUES: 'pSCustomFieldsValues/DELETE_PSCUSTOMFIELDSVALUES',
  RESET: 'pSCustomFieldsValues/RESET'
};

const initialState = {
  loading: false,
  errorMessage: null,
  entities: [] as ReadonlyArray<IPSCustomFieldsValues>,
  entity: defaultValue,
  updating: false,
  updateSuccess: false
};

export type PSCustomFieldsValuesState = Readonly<typeof initialState>;

// Reducer

export default (state: PSCustomFieldsValuesState = initialState, action): PSCustomFieldsValuesState => {
  switch (action.type) {
    case REQUEST(ACTION_TYPES.FETCH_PSCUSTOMFIELDSVALUES_LIST):
    case REQUEST(ACTION_TYPES.FETCH_PSCUSTOMFIELDSVALUES):
      return {
        ...state,
        errorMessage: null,
        updateSuccess: false,
        loading: true
      };
    case REQUEST(ACTION_TYPES.CREATE_PSCUSTOMFIELDSVALUES):
    case REQUEST(ACTION_TYPES.UPDATE_PSCUSTOMFIELDSVALUES):
    case REQUEST(ACTION_TYPES.DELETE_PSCUSTOMFIELDSVALUES):
      return {
        ...state,
        errorMessage: null,
        updateSuccess: false,
        updating: true
      };
    case FAILURE(ACTION_TYPES.FETCH_PSCUSTOMFIELDSVALUES_LIST):
    case FAILURE(ACTION_TYPES.FETCH_PSCUSTOMFIELDSVALUES):
    case FAILURE(ACTION_TYPES.CREATE_PSCUSTOMFIELDSVALUES):
    case FAILURE(ACTION_TYPES.UPDATE_PSCUSTOMFIELDSVALUES):
    case FAILURE(ACTION_TYPES.DELETE_PSCUSTOMFIELDSVALUES):
      return {
        ...state,
        loading: false,
        updating: false,
        updateSuccess: false,
        errorMessage: action.payload
      };
    case SUCCESS(ACTION_TYPES.FETCH_PSCUSTOMFIELDSVALUES_LIST):
      return {
        ...state,
        loading: false,
        entities: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.FETCH_PSCUSTOMFIELDSVALUES):
      return {
        ...state,
        loading: false,
        entity: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.CREATE_PSCUSTOMFIELDSVALUES):
    case SUCCESS(ACTION_TYPES.UPDATE_PSCUSTOMFIELDSVALUES):
      return {
        ...state,
        updating: false,
        updateSuccess: true,
        entity: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.DELETE_PSCUSTOMFIELDSVALUES):
      return {
        ...state,
        updating: false,
        updateSuccess: true,
        entity: {}
      };
    case ACTION_TYPES.RESET:
      return {
        ...initialState
      };
    default:
      return state;
  }
};

const apiUrl = 'api/ps-custom-fields-values';

// Actions

export const getEntities: ICrudGetAllAction<IPSCustomFieldsValues> = (page, size, sort) => ({
  type: ACTION_TYPES.FETCH_PSCUSTOMFIELDSVALUES_LIST,
  payload: axios.get<IPSCustomFieldsValues>(`${apiUrl}?cacheBuster=${new Date().getTime()}`)
});

export const getEntity: ICrudGetAction<IPSCustomFieldsValues> = id => {
  const requestUrl = `${apiUrl}/${id}`;
  return {
    type: ACTION_TYPES.FETCH_PSCUSTOMFIELDSVALUES,
    payload: axios.get<IPSCustomFieldsValues>(requestUrl)
  };
};

export const createEntity: ICrudPutAction<IPSCustomFieldsValues> = entity => async dispatch => {
  const result = await dispatch({
    type: ACTION_TYPES.CREATE_PSCUSTOMFIELDSVALUES,
    payload: axios.post(apiUrl, cleanEntity(entity))
  });
  dispatch(getEntities());
  return result;
};

export const updateEntity: ICrudPutAction<IPSCustomFieldsValues> = entity => async dispatch => {
  const result = await dispatch({
    type: ACTION_TYPES.UPDATE_PSCUSTOMFIELDSVALUES,
    payload: axios.put(apiUrl, cleanEntity(entity))
  });
  dispatch(getEntities());
  return result;
};

export const deleteEntity: ICrudDeleteAction<IPSCustomFieldsValues> = id => async dispatch => {
  const requestUrl = `${apiUrl}/${id}`;
  const result = await dispatch({
    type: ACTION_TYPES.DELETE_PSCUSTOMFIELDSVALUES,
    payload: axios.delete(requestUrl)
  });
  dispatch(getEntities());
  return result;
};

export const reset = () => ({
  type: ACTION_TYPES.RESET
});
