import React from 'react';
import { Switch } from 'react-router-dom';

import ErrorBoundaryRoute from 'app/shared/error/error-boundary-route';

import PSCustomFieldsTemplate from './ps-custom-fields-template';
import PSCustomFieldsTemplateDetail from './ps-custom-fields-template-detail';
import PSCustomFieldsTemplateUpdate from './ps-custom-fields-template-update';
import PSCustomFieldsTemplateDeleteDialog from './ps-custom-fields-template-delete-dialog';

const Routes = ({ match }) => (
  <>
    <Switch>
      <ErrorBoundaryRoute exact path={`${match.url}/new`} component={PSCustomFieldsTemplateUpdate} />
      <ErrorBoundaryRoute exact path={`${match.url}/:id/edit`} component={PSCustomFieldsTemplateUpdate} />
      <ErrorBoundaryRoute exact path={`${match.url}/:id`} component={PSCustomFieldsTemplateDetail} />
      <ErrorBoundaryRoute path={match.url} component={PSCustomFieldsTemplate} />
    </Switch>
    <ErrorBoundaryRoute path={`${match.url}/:id/delete`} component={PSCustomFieldsTemplateDeleteDialog} />
  </>
);

export default Routes;
