import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Row, Col } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { ICrudGetAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntity } from './ps-entity.reducer';
import { IPSEntity } from 'app/shared/model/ps-entity.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';

export interface IPSEntityDetailProps extends StateProps, DispatchProps, RouteComponentProps<{ id: number }> {}

export class PSEntityDetail extends React.Component<IPSEntityDetailProps> {
  componentDidMount() {
    this.props.getEntity(this.props.match.params.id);
  }

  render() {
    const { pSEntityEntity } = this.props;
    return (
      <Row>
        <Col md="8">
          <h2>
            PSEntity [<b>{pSEntityEntity.id}</b>]
          </h2>
          <dl className="jh-entity-details">
            <dt>
              <span id="name">Name</span>
            </dt>
            <dd>{pSEntityEntity.name}</dd>
            <dt>Psentitytemplateid</dt>
            <dd>{pSEntityEntity.psentitytemplateid ? pSEntityEntity.psentitytemplateid.id : ''}</dd>
          </dl>
          <Button tag={Link} to="/entity/ps-entity" replace color="info">
            <FontAwesomeIcon icon="arrow-left" /> <span className="d-none d-md-inline">Back</span>
          </Button>&nbsp;
          <Button tag={Link} to={`/entity/ps-entity/${pSEntityEntity.id}/edit`} replace color="primary">
            <FontAwesomeIcon icon="pencil-alt" /> <span className="d-none d-md-inline">Edit</span>
          </Button>
        </Col>
      </Row>
    );
  }
}

const mapStateToProps = ({ pSEntity }: IRootState) => ({
  pSEntityEntity: pSEntity.entity
});

const mapDispatchToProps = { getEntity };

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PSEntityDetail);
