import axios from 'axios';
import { ICrudGetAction, ICrudGetAllAction, ICrudPutAction, ICrudDeleteAction } from 'react-jhipster';

import { cleanEntity } from 'app/shared/util/entity-utils';
import { REQUEST, SUCCESS, FAILURE } from 'app/shared/reducers/action-type.util';

import { IPSEntity, defaultValue } from 'app/shared/model/ps-entity.model';

export const ACTION_TYPES = {
  FETCH_PSENTITY_LIST: 'pSEntity/FETCH_PSENTITY_LIST',
  FETCH_PSENTITY: 'pSEntity/FETCH_PSENTITY',
  CREATE_PSENTITY: 'pSEntity/CREATE_PSENTITY',
  UPDATE_PSENTITY: 'pSEntity/UPDATE_PSENTITY',
  DELETE_PSENTITY: 'pSEntity/DELETE_PSENTITY',
  RESET: 'pSEntity/RESET'
};

const initialState = {
  loading: false,
  errorMessage: null,
  entities: [] as ReadonlyArray<IPSEntity>,
  entity: defaultValue,
  updating: false,
  updateSuccess: false
};

export type PSEntityState = Readonly<typeof initialState>;

// Reducer

export default (state: PSEntityState = initialState, action): PSEntityState => {
  switch (action.type) {
    case REQUEST(ACTION_TYPES.FETCH_PSENTITY_LIST):
    case REQUEST(ACTION_TYPES.FETCH_PSENTITY):
      return {
        ...state,
        errorMessage: null,
        updateSuccess: false,
        loading: true
      };
    case REQUEST(ACTION_TYPES.CREATE_PSENTITY):
    case REQUEST(ACTION_TYPES.UPDATE_PSENTITY):
    case REQUEST(ACTION_TYPES.DELETE_PSENTITY):
      return {
        ...state,
        errorMessage: null,
        updateSuccess: false,
        updating: true
      };
    case FAILURE(ACTION_TYPES.FETCH_PSENTITY_LIST):
    case FAILURE(ACTION_TYPES.FETCH_PSENTITY):
    case FAILURE(ACTION_TYPES.CREATE_PSENTITY):
    case FAILURE(ACTION_TYPES.UPDATE_PSENTITY):
    case FAILURE(ACTION_TYPES.DELETE_PSENTITY):
      return {
        ...state,
        loading: false,
        updating: false,
        updateSuccess: false,
        errorMessage: action.payload
      };
    case SUCCESS(ACTION_TYPES.FETCH_PSENTITY_LIST):
      return {
        ...state,
        loading: false,
        entities: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.FETCH_PSENTITY):
      return {
        ...state,
        loading: false,
        entity: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.CREATE_PSENTITY):
    case SUCCESS(ACTION_TYPES.UPDATE_PSENTITY):
      return {
        ...state,
        updating: false,
        updateSuccess: true,
        entity: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.DELETE_PSENTITY):
      return {
        ...state,
        updating: false,
        updateSuccess: true,
        entity: {}
      };
    case ACTION_TYPES.RESET:
      return {
        ...initialState
      };
    default:
      return state;
  }
};

const apiUrl = 'api/ps-entities';

// Actions

export const getEntities: ICrudGetAllAction<IPSEntity> = (page, size, sort) => ({
  type: ACTION_TYPES.FETCH_PSENTITY_LIST,
  payload: axios.get<IPSEntity>(`${apiUrl}?cacheBuster=${new Date().getTime()}`)
});

export const getEntity: ICrudGetAction<IPSEntity> = id => {
  const requestUrl = `${apiUrl}/${id}`;
  return {
    type: ACTION_TYPES.FETCH_PSENTITY,
    payload: axios.get<IPSEntity>(requestUrl)
  };
};

export const createEntity: ICrudPutAction<IPSEntity> = entity => async dispatch => {
  const result = await dispatch({
    type: ACTION_TYPES.CREATE_PSENTITY,
    payload: axios.post(apiUrl, cleanEntity(entity))
  });
  dispatch(getEntities());
  return result;
};

export const updateEntity: ICrudPutAction<IPSEntity> = entity => async dispatch => {
  const result = await dispatch({
    type: ACTION_TYPES.UPDATE_PSENTITY,
    payload: axios.put(apiUrl, cleanEntity(entity))
  });
  dispatch(getEntities());
  return result;
};

export const deleteEntity: ICrudDeleteAction<IPSEntity> = id => async dispatch => {
  const requestUrl = `${apiUrl}/${id}`;
  const result = await dispatch({
    type: ACTION_TYPES.DELETE_PSENTITY,
    payload: axios.delete(requestUrl)
  });
  dispatch(getEntities());
  return result;
};

export const reset = () => ({
  type: ACTION_TYPES.RESET
});
