import React from 'react';
import { Switch } from 'react-router-dom';

import ErrorBoundaryRoute from 'app/shared/error/error-boundary-route';

import PSEntity from './ps-entity';
import PSEntityDetail from './ps-entity-detail';
import PSEntityUpdate from './ps-entity-update';
import PSEntityDeleteDialog from './ps-entity-delete-dialog';

const Routes = ({ match }) => (
  <>
    <Switch>
      <ErrorBoundaryRoute exact path={`${match.url}/new`} component={PSEntityUpdate} />
      <ErrorBoundaryRoute exact path={`${match.url}/:id/edit`} component={PSEntityUpdate} />
      <ErrorBoundaryRoute exact path={`${match.url}/:id`} component={PSEntityDetail} />
      <ErrorBoundaryRoute path={match.url} component={PSEntity} />
    </Switch>
    <ErrorBoundaryRoute path={`${match.url}/:id/delete`} component={PSEntityDeleteDialog} />
  </>
);

export default Routes;
