import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Col, Row, Table } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { ICrudGetAllAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntities } from './ps-custom-fields-values.reducer';
import { IPSCustomFieldsValues } from 'app/shared/model/ps-custom-fields-values.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';

export interface IPSCustomFieldsValuesProps extends StateProps, DispatchProps, RouteComponentProps<{ url: string }> {}

export class PSCustomFieldsValues extends React.Component<IPSCustomFieldsValuesProps> {
  componentDidMount() {
    this.props.getEntities();
  }

  render() {
    const { pSCustomFieldsValuesList, match } = this.props;
    return (
      <div>
        <h2 id="ps-custom-fields-values-heading">
          PS Custom Fields Values
          <Link to={`${match.url}/new`} className="btn btn-primary float-right jh-create-entity" id="jh-create-entity">
            <FontAwesomeIcon icon="plus" />&nbsp; Create new PS Custom Fields Values
          </Link>
        </h2>
        <div className="table-responsive">
          <Table responsive>
            <thead>
              <tr>
                <th>ID</th>
                <th>Value</th>
                <th>Psentity</th>
                <th>Pscustomfieldstemplate</th>
                <th />
              </tr>
            </thead>
            <tbody>
              {pSCustomFieldsValuesList.map((pSCustomFieldsValues, i) => (
                <tr key={`entity-${i}`}>
                  <td>
                    <Button tag={Link} to={`${match.url}/${pSCustomFieldsValues.id}`} color="link" size="sm">
                      {pSCustomFieldsValues.id}
                    </Button>
                  </td>
                  <td>{pSCustomFieldsValues.value}</td>
                  <td>
                    {pSCustomFieldsValues.psentity ? (
                      <Link to={`pSEntity/${pSCustomFieldsValues.psentity.id}`}>{pSCustomFieldsValues.psentity.id}</Link>
                    ) : (
                      ''
                    )}
                  </td>
                  <td>
                    {pSCustomFieldsValues.pscustomfieldstemplate ? (
                      <Link to={`pSCustomFieldsTemplate/${pSCustomFieldsValues.pscustomfieldstemplate.id}`}>
                        {pSCustomFieldsValues.pscustomfieldstemplate.id}
                      </Link>
                    ) : (
                      ''
                    )}
                  </td>
                  <td className="text-right">
                    <div className="btn-group flex-btn-group-container">
                      <Button tag={Link} to={`${match.url}/${pSCustomFieldsValues.id}`} color="info" size="sm">
                        <FontAwesomeIcon icon="eye" /> <span className="d-none d-md-inline">View</span>
                      </Button>
                      <Button tag={Link} to={`${match.url}/${pSCustomFieldsValues.id}/edit`} color="primary" size="sm">
                        <FontAwesomeIcon icon="pencil-alt" /> <span className="d-none d-md-inline">Edit</span>
                      </Button>
                      <Button tag={Link} to={`${match.url}/${pSCustomFieldsValues.id}/delete`} color="danger" size="sm">
                        <FontAwesomeIcon icon="trash" /> <span className="d-none d-md-inline">Delete</span>
                      </Button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ pSCustomFieldsValues }: IRootState) => ({
  pSCustomFieldsValuesList: pSCustomFieldsValues.entities
});

const mapDispatchToProps = {
  getEntities
};

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PSCustomFieldsValues);
