import React from 'react';
import { Switch } from 'react-router-dom';

import ErrorBoundaryRoute from 'app/shared/error/error-boundary-route';

import PSCustomFieldsValues from './ps-custom-fields-values';
import PSCustomFieldsValuesDetail from './ps-custom-fields-values-detail';
import PSCustomFieldsValuesUpdate from './ps-custom-fields-values-update';
import PSCustomFieldsValuesDeleteDialog from './ps-custom-fields-values-delete-dialog';

const Routes = ({ match }) => (
  <>
    <Switch>
      <ErrorBoundaryRoute exact path={`${match.url}/new`} component={PSCustomFieldsValuesUpdate} />
      <ErrorBoundaryRoute exact path={`${match.url}/:id/edit`} component={PSCustomFieldsValuesUpdate} />
      <ErrorBoundaryRoute exact path={`${match.url}/:id`} component={PSCustomFieldsValuesDetail} />
      <ErrorBoundaryRoute path={match.url} component={PSCustomFieldsValues} />
    </Switch>
    <ErrorBoundaryRoute path={`${match.url}/:id/delete`} component={PSCustomFieldsValuesDeleteDialog} />
  </>
);

export default Routes;
