import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Col, Row, Table } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { ICrudGetAllAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntities } from './ps-entity-template.reducer';
import { IPSEntityTemplate } from 'app/shared/model/ps-entity-template.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';

export interface IPSEntityTemplateProps extends StateProps, DispatchProps, RouteComponentProps<{ url: string }> {}

export class PSEntityTemplate extends React.Component<IPSEntityTemplateProps> {
  componentDidMount() {
    this.props.getEntities();
  }

  render() {
    const { pSEntityTemplateList, match } = this.props;
    return (
      <div>
        <h2 id="ps-entity-template-heading">
          PS Entity Templates
          <Link to={`${match.url}/new`} className="btn btn-primary float-right jh-create-entity" id="jh-create-entity">
            <FontAwesomeIcon icon="plus" />&nbsp; Create new PS Entity Template
          </Link>
        </h2>
        <div className="table-responsive">
          <Table responsive>
            <thead>
              <tr>
                <th>ID</th>
                <th>Name</th>
                <th>Parentid</th>
                <th />
              </tr>
            </thead>
            <tbody>
              {pSEntityTemplateList.map((pSEntityTemplate, i) => (
                <tr key={`entity-${i}`}>
                  <td>
                    <Button tag={Link} to={`${match.url}/${pSEntityTemplate.id}`} color="link" size="sm">
                      {pSEntityTemplate.id}
                    </Button>
                  </td>
                  <td>{pSEntityTemplate.name}</td>
                  <td>
                    {pSEntityTemplate.parentid ? (
                      <Link to={`pSEntityTemplate/${pSEntityTemplate.parentid.id}`}>{pSEntityTemplate.parentid.id}</Link>
                    ) : (
                      ''
                    )}
                  </td>
                  <td className="text-right">
                    <div className="btn-group flex-btn-group-container">
                      <Button tag={Link} to={`${match.url}/${pSEntityTemplate.id}`} color="info" size="sm">
                        <FontAwesomeIcon icon="eye" /> <span className="d-none d-md-inline">View</span>
                      </Button>
                      <Button tag={Link} to={`${match.url}/${pSEntityTemplate.id}/edit`} color="primary" size="sm">
                        <FontAwesomeIcon icon="pencil-alt" /> <span className="d-none d-md-inline">Edit</span>
                      </Button>
                      <Button tag={Link} to={`${match.url}/${pSEntityTemplate.id}/delete`} color="danger" size="sm">
                        <FontAwesomeIcon icon="trash" /> <span className="d-none d-md-inline">Delete</span>
                      </Button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ pSEntityTemplate }: IRootState) => ({
  pSEntityTemplateList: pSEntityTemplate.entities
});

const mapDispatchToProps = {
  getEntities
};

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PSEntityTemplate);
