import axios from 'axios';
import { ICrudGetAction, ICrudGetAllAction, ICrudPutAction, ICrudDeleteAction } from 'react-jhipster';

import { cleanEntity } from 'app/shared/util/entity-utils';
import { REQUEST, SUCCESS, FAILURE } from 'app/shared/reducers/action-type.util';

import { IPSEntityTemplate, defaultValue } from 'app/shared/model/ps-entity-template.model';

export const ACTION_TYPES = {
  FETCH_PSENTITYTEMPLATE_LIST: 'pSEntityTemplate/FETCH_PSENTITYTEMPLATE_LIST',
  FETCH_PSENTITYTEMPLATE: 'pSEntityTemplate/FETCH_PSENTITYTEMPLATE',
  CREATE_PSENTITYTEMPLATE: 'pSEntityTemplate/CREATE_PSENTITYTEMPLATE',
  UPDATE_PSENTITYTEMPLATE: 'pSEntityTemplate/UPDATE_PSENTITYTEMPLATE',
  DELETE_PSENTITYTEMPLATE: 'pSEntityTemplate/DELETE_PSENTITYTEMPLATE',
  RESET: 'pSEntityTemplate/RESET'
};

const initialState = {
  loading: false,
  errorMessage: null,
  entities: [] as ReadonlyArray<IPSEntityTemplate>,
  entity: defaultValue,
  updating: false,
  updateSuccess: false
};

export type PSEntityTemplateState = Readonly<typeof initialState>;

// Reducer

export default (state: PSEntityTemplateState = initialState, action): PSEntityTemplateState => {
  switch (action.type) {
    case REQUEST(ACTION_TYPES.FETCH_PSENTITYTEMPLATE_LIST):
    case REQUEST(ACTION_TYPES.FETCH_PSENTITYTEMPLATE):
      return {
        ...state,
        errorMessage: null,
        updateSuccess: false,
        loading: true
      };
    case REQUEST(ACTION_TYPES.CREATE_PSENTITYTEMPLATE):
    case REQUEST(ACTION_TYPES.UPDATE_PSENTITYTEMPLATE):
    case REQUEST(ACTION_TYPES.DELETE_PSENTITYTEMPLATE):
      return {
        ...state,
        errorMessage: null,
        updateSuccess: false,
        updating: true
      };
    case FAILURE(ACTION_TYPES.FETCH_PSENTITYTEMPLATE_LIST):
    case FAILURE(ACTION_TYPES.FETCH_PSENTITYTEMPLATE):
    case FAILURE(ACTION_TYPES.CREATE_PSENTITYTEMPLATE):
    case FAILURE(ACTION_TYPES.UPDATE_PSENTITYTEMPLATE):
    case FAILURE(ACTION_TYPES.DELETE_PSENTITYTEMPLATE):
      return {
        ...state,
        loading: false,
        updating: false,
        updateSuccess: false,
        errorMessage: action.payload
      };
    case SUCCESS(ACTION_TYPES.FETCH_PSENTITYTEMPLATE_LIST):
      return {
        ...state,
        loading: false,
        entities: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.FETCH_PSENTITYTEMPLATE):
      return {
        ...state,
        loading: false,
        entity: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.CREATE_PSENTITYTEMPLATE):
    case SUCCESS(ACTION_TYPES.UPDATE_PSENTITYTEMPLATE):
      return {
        ...state,
        updating: false,
        updateSuccess: true,
        entity: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.DELETE_PSENTITYTEMPLATE):
      return {
        ...state,
        updating: false,
        updateSuccess: true,
        entity: {}
      };
    case ACTION_TYPES.RESET:
      return {
        ...initialState
      };
    default:
      return state;
  }
};

const apiUrl = 'api/ps-entity-templates';

// Actions

export const getEntities: ICrudGetAllAction<IPSEntityTemplate> = (page, size, sort) => ({
  type: ACTION_TYPES.FETCH_PSENTITYTEMPLATE_LIST,
  payload: axios.get<IPSEntityTemplate>(`${apiUrl}?cacheBuster=${new Date().getTime()}`)
});

export const getEntity: ICrudGetAction<IPSEntityTemplate> = id => {
  const requestUrl = `${apiUrl}/${id}`;
  return {
    type: ACTION_TYPES.FETCH_PSENTITYTEMPLATE,
    payload: axios.get<IPSEntityTemplate>(requestUrl)
  };
};

export const createEntity: ICrudPutAction<IPSEntityTemplate> = entity => async dispatch => {
  const result = await dispatch({
    type: ACTION_TYPES.CREATE_PSENTITYTEMPLATE,
    payload: axios.post(apiUrl, cleanEntity(entity))
  });
  dispatch(getEntities());
  return result;
};

export const updateEntity: ICrudPutAction<IPSEntityTemplate> = entity => async dispatch => {
  const result = await dispatch({
    type: ACTION_TYPES.UPDATE_PSENTITYTEMPLATE,
    payload: axios.put(apiUrl, cleanEntity(entity))
  });
  dispatch(getEntities());
  return result;
};

export const deleteEntity: ICrudDeleteAction<IPSEntityTemplate> = id => async dispatch => {
  const requestUrl = `${apiUrl}/${id}`;
  const result = await dispatch({
    type: ACTION_TYPES.DELETE_PSENTITYTEMPLATE,
    payload: axios.delete(requestUrl)
  });
  dispatch(getEntities());
  return result;
};

export const reset = () => ({
  type: ACTION_TYPES.RESET
});
