import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Row, Col } from 'reactstrap';
// tslint:disable-next-line:no-unused-variable
import { ICrudGetAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import { IRootState } from 'app/shared/reducers';
import { getEntity } from './ps-custom-fields-template.reducer';
import { IPSCustomFieldsTemplate } from 'app/shared/model/ps-custom-fields-template.model';
// tslint:disable-next-line:no-unused-variable
import { APP_DATE_FORMAT, APP_LOCAL_DATE_FORMAT } from 'app/config/constants';

export interface IPSCustomFieldsTemplateDetailProps extends StateProps, DispatchProps, RouteComponentProps<{ id: number }> {}

export class PSCustomFieldsTemplateDetail extends React.Component<IPSCustomFieldsTemplateDetailProps> {
  componentDidMount() {
    this.props.getEntity(this.props.match.params.id);
  }

  render() {
    const { pSCustomFieldsTemplateEntity } = this.props;
    return (
      <Row>
        <Col md="8">
          <h2>
            PSCustomFieldsTemplate [<b>{pSCustomFieldsTemplateEntity.id}</b>]
          </h2>
          <dl className="jh-entity-details">
            <dt>
              <span id="name">Name</span>
            </dt>
            <dd>{pSCustomFieldsTemplateEntity.name}</dd>
            <dt>
              <span id="type">Type</span>
            </dt>
            <dd>{pSCustomFieldsTemplateEntity.type}</dd>
            <dt>Psentitytemplateid</dt>
            <dd>{pSCustomFieldsTemplateEntity.psentitytemplateid ? pSCustomFieldsTemplateEntity.psentitytemplateid.id : ''}</dd>
          </dl>
          <Button tag={Link} to="/entity/ps-custom-fields-template" replace color="info">
            <FontAwesomeIcon icon="arrow-left" /> <span className="d-none d-md-inline">Back</span>
          </Button>&nbsp;
          <Button tag={Link} to={`/entity/ps-custom-fields-template/${pSCustomFieldsTemplateEntity.id}/edit`} replace color="primary">
            <FontAwesomeIcon icon="pencil-alt" /> <span className="d-none d-md-inline">Edit</span>
          </Button>
        </Col>
      </Row>
    );
  }
}

const mapStateToProps = ({ pSCustomFieldsTemplate }: IRootState) => ({
  pSCustomFieldsTemplateEntity: pSCustomFieldsTemplate.entity
});

const mapDispatchToProps = { getEntity };

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PSCustomFieldsTemplateDetail);
