import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Row, Col, Label } from 'reactstrap';
import { AvForm, AvGroup, AvInput, AvField } from 'availity-reactstrap-validation';
// tslint:disable-next-line:no-unused-variable
import { ICrudGetAction, ICrudGetAllAction, ICrudPutAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { IRootState } from 'app/shared/reducers';

import { IPSEntity } from 'app/shared/model/ps-entity.model';
import { getEntities as getPSEntities } from 'app/entities/ps-entity/ps-entity.reducer';
import { IPSCustomFieldsTemplate } from 'app/shared/model/ps-custom-fields-template.model';
import { getEntities as getPSCustomFieldsTemplates } from 'app/entities/ps-custom-fields-template/ps-custom-fields-template.reducer';
import { getEntity, updateEntity, createEntity, reset } from './ps-custom-fields-values.reducer';
import { IPSCustomFieldsValues } from 'app/shared/model/ps-custom-fields-values.model';
// tslint:disable-next-line:no-unused-variable
import { convertDateTimeFromServer } from 'app/shared/util/date-utils';
import { keysToValues } from 'app/shared/util/entity-utils';

export interface IPSCustomFieldsValuesUpdateProps extends StateProps, DispatchProps, RouteComponentProps<{ id: number }> {}

export interface IPSCustomFieldsValuesUpdateState {
  isNew: boolean;
  psentityId: number;
  pscustomfieldstemplateId: number;
}

export class PSCustomFieldsValuesUpdate extends React.Component<IPSCustomFieldsValuesUpdateProps, IPSCustomFieldsValuesUpdateState> {
  constructor(props) {
    super(props);
    this.state = {
      psentityId: 0,
      pscustomfieldstemplateId: 0,
      isNew: !this.props.match.params || !this.props.match.params.id
    };
  }

  componentDidMount() {
    if (this.state.isNew) {
      this.props.reset();
    } else {
      this.props.getEntity(this.props.match.params.id);
    }

    this.props.getPSEntities();
    this.props.getPSCustomFieldsTemplates();
  }

  saveEntity = (event, errors, values) => {
    if (errors.length === 0) {
      const { pSCustomFieldsValuesEntity } = this.props;
      const entity = {
        ...pSCustomFieldsValuesEntity,
        ...values
      };

      if (this.state.isNew) {
        this.props.createEntity(entity);
      } else {
        this.props.updateEntity(entity);
      }
      this.handleClose();
    }
  };

  handleClose = () => {
    this.props.history.push('/entity/ps-custom-fields-values');
  };

  psentityUpdate = element => {
    const id = element.target.value.toString();
    if (id === '') {
      this.setState({
        psentityId: -1
      });
    } else {
      for (const i in this.props.pSEntities) {
        if (id === this.props.pSEntities[i].id.toString()) {
          this.setState({
            psentityId: this.props.pSEntities[i].id
          });
        }
      }
    }
  };

  pscustomfieldstemplateUpdate = element => {
    const id = element.target.value.toString();
    if (id === '') {
      this.setState({
        pscustomfieldstemplateId: -1
      });
    } else {
      for (const i in this.props.pSCustomFieldsTemplates) {
        if (id === this.props.pSCustomFieldsTemplates[i].id.toString()) {
          this.setState({
            pscustomfieldstemplateId: this.props.pSCustomFieldsTemplates[i].id
          });
        }
      }
    }
  };

  render() {
    const { pSCustomFieldsValuesEntity, pSEntities, pSCustomFieldsTemplates, loading, updating } = this.props;
    const { isNew } = this.state;

    return (
      <div>
        <Row className="justify-content-center">
          <Col md="8">
            <h2 id="jhipster5App.pSCustomFieldsValues.home.createOrEditLabel">Create or edit a PSCustomFieldsValues</h2>
          </Col>
        </Row>
        <Row className="justify-content-center">
          <Col md="8">
            {loading ? (
              <p>Loading...</p>
            ) : (
              <AvForm model={isNew ? {} : pSCustomFieldsValuesEntity} onSubmit={this.saveEntity}>
                {!isNew ? (
                  <AvGroup>
                    <Label for="id">ID</Label>
                    <AvInput id="ps-custom-fields-values-id" type="text" className="form-control" name="id" required readOnly />
                  </AvGroup>
                ) : null}
                <AvGroup>
                  <Label id="valueLabel" for="value">
                    Value
                  </Label>
                  <AvField
                    id="ps-custom-fields-values-value"
                    type="text"
                    name="value"
                    validate={{
                      required: { value: true, errorMessage: 'This field is required.' }
                    }}
                  />
                </AvGroup>
                <AvGroup>
                  <Label for="psentity.id">Psentity</Label>
                  <AvInput
                    id="ps-custom-fields-values-psentity"
                    type="select"
                    className="form-control"
                    name="psentity.id"
                    onChange={this.psentityUpdate}
                  >
                    <option value="" key="0" />
                    {pSEntities
                      ? pSEntities.map(otherEntity => (
                          <option value={otherEntity.id} key={otherEntity.id}>
                            {otherEntity.id}
                          </option>
                        ))
                      : null}
                  </AvInput>
                </AvGroup>
                <AvGroup>
                  <Label for="pscustomfieldstemplate.id">Pscustomfieldstemplate</Label>
                  <AvInput
                    id="ps-custom-fields-values-pscustomfieldstemplate"
                    type="select"
                    className="form-control"
                    name="pscustomfieldstemplate.id"
                    onChange={this.pscustomfieldstemplateUpdate}
                  >
                    <option value="" key="0" />
                    {pSCustomFieldsTemplates
                      ? pSCustomFieldsTemplates.map(otherEntity => (
                          <option value={otherEntity.id} key={otherEntity.id}>
                            {otherEntity.id}
                          </option>
                        ))
                      : null}
                  </AvInput>
                </AvGroup>
                <Button tag={Link} id="cancel-save" to="/entity/ps-custom-fields-values" replace color="info">
                  <FontAwesomeIcon icon="arrow-left" />&nbsp;
                  <span className="d-none d-md-inline">Back</span>
                </Button>
                &nbsp;
                <Button color="primary" id="save-entity" type="submit" disabled={updating}>
                  <FontAwesomeIcon icon="save" />&nbsp; Save
                </Button>
              </AvForm>
            )}
          </Col>
        </Row>
      </div>
    );
  }
}

const mapStateToProps = (storeState: IRootState) => ({
  pSEntities: storeState.pSEntity.entities,
  pSCustomFieldsTemplates: storeState.pSCustomFieldsTemplate.entities,
  pSCustomFieldsValuesEntity: storeState.pSCustomFieldsValues.entity,
  loading: storeState.pSCustomFieldsValues.loading,
  updating: storeState.pSCustomFieldsValues.updating
});

const mapDispatchToProps = {
  getPSEntities,
  getPSCustomFieldsTemplates,
  getEntity,
  updateEntity,
  createEntity,
  reset
};

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PSCustomFieldsValuesUpdate);
