import React from 'react';
import { Switch } from 'react-router-dom';

import ErrorBoundaryRoute from 'app/shared/error/error-boundary-route';

import PSEntityTemplate from './ps-entity-template';
import PSEntityTemplateDetail from './ps-entity-template-detail';
import PSEntityTemplateUpdate from './ps-entity-template-update';
import PSEntityTemplateDeleteDialog from './ps-entity-template-delete-dialog';

const Routes = ({ match }) => (
  <>
    <Switch>
      <ErrorBoundaryRoute exact path={`${match.url}/new`} component={PSEntityTemplateUpdate} />
      <ErrorBoundaryRoute exact path={`${match.url}/:id/edit`} component={PSEntityTemplateUpdate} />
      <ErrorBoundaryRoute exact path={`${match.url}/:id`} component={PSEntityTemplateDetail} />
      <ErrorBoundaryRoute path={match.url} component={PSEntityTemplate} />
    </Switch>
    <ErrorBoundaryRoute path={`${match.url}/:id/delete`} component={PSEntityTemplateDeleteDialog} />
  </>
);

export default Routes;
