import React from 'react';
import { Switch } from 'react-router-dom';

// tslint:disable-next-line:no-unused-variable
import ErrorBoundaryRoute from 'app/shared/error/error-boundary-route';

import PSEntityTemplate from './ps-entity-template';
import PSCustomFieldsTemplate from './ps-custom-fields-template';
import PSEntity from './ps-entity';
import PSCustomFieldsValues from './ps-custom-fields-values';
/* jhipster-needle-add-route-import - JHipster will add routes here */

const Routes = ({ match }) => (
  <div>
    <Switch>
      {/* prettier-ignore */}
      <ErrorBoundaryRoute path={`${match.url}/ps-entity-template`} component={PSEntityTemplate} />
      <ErrorBoundaryRoute path={`${match.url}/ps-custom-fields-template`} component={PSCustomFieldsTemplate} />
      <ErrorBoundaryRoute path={`${match.url}/ps-entity`} component={PSEntity} />
      <ErrorBoundaryRoute path={`${match.url}/ps-custom-fields-values`} component={PSCustomFieldsValues} />
      {/* jhipster-needle-add-route-path - JHipster will routes here */}
    </Switch>
  </div>
);

export default Routes;
