import React from 'react';
import { connect } from 'react-redux';
import { Link, RouteComponentProps } from 'react-router-dom';
import { Button, Row, Col, Label } from 'reactstrap';
import { AvForm, AvGroup, AvInput, AvField } from 'availity-reactstrap-validation';
// tslint:disable-next-line:no-unused-variable
import { ICrudGetAction, ICrudGetAllAction, ICrudPutAction } from 'react-jhipster';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { IRootState } from 'app/shared/reducers';

import { IPSEntityTemplate } from 'app/shared/model/ps-entity-template.model';
import { getEntities as getPSEntityTemplates } from 'app/entities/ps-entity-template/ps-entity-template.reducer';
import { getEntity, updateEntity, createEntity, reset } from './ps-entity.reducer';
import { IPSEntity } from 'app/shared/model/ps-entity.model';
// tslint:disable-next-line:no-unused-variable
import { convertDateTimeFromServer } from 'app/shared/util/date-utils';
import { keysToValues } from 'app/shared/util/entity-utils';

export interface IPSEntityUpdateProps extends StateProps, DispatchProps, RouteComponentProps<{ id: number }> {}

export interface IPSEntityUpdateState {
  isNew: boolean;
  psentitytemplateidId: number;
}

export class PSEntityUpdate extends React.Component<IPSEntityUpdateProps, IPSEntityUpdateState> {
  constructor(props) {
    super(props);
    this.state = {
      psentitytemplateidId: 0,
      isNew: !this.props.match.params || !this.props.match.params.id
    };
  }

  componentDidMount() {
    if (this.state.isNew) {
      this.props.reset();
    } else {
      this.props.getEntity(this.props.match.params.id);
    }

    this.props.getPSEntityTemplates();
  }

  saveEntity = (event, errors, values) => {
    if (errors.length === 0) {
      const { pSEntityEntity } = this.props;
      const entity = {
        ...pSEntityEntity,
        ...values
      };

      if (this.state.isNew) {
        this.props.createEntity(entity);
      } else {
        this.props.updateEntity(entity);
      }
      this.handleClose();
    }
  };

  handleClose = () => {
    this.props.history.push('/entity/ps-entity');
  };

  psentitytemplateidUpdate = element => {
    const id = element.target.value.toString();
    if (id === '') {
      this.setState({
        psentitytemplateidId: -1
      });
    } else {
      for (const i in this.props.pSEntityTemplates) {
        if (id === this.props.pSEntityTemplates[i].id.toString()) {
          this.setState({
            psentitytemplateidId: this.props.pSEntityTemplates[i].id
          });
        }
      }
    }
  };

  render() {
    const { pSEntityEntity, pSEntityTemplates, loading, updating } = this.props;
    const { isNew } = this.state;

    return (
      <div>
        <Row className="justify-content-center">
          <Col md="8">
            <h2 id="jhipster5App.pSEntity.home.createOrEditLabel">Create or edit a PSEntity</h2>
          </Col>
        </Row>
        <Row className="justify-content-center">
          <Col md="8">
            {loading ? (
              <p>Loading...</p>
            ) : (
              <AvForm model={isNew ? {} : pSEntityEntity} onSubmit={this.saveEntity}>
                {!isNew ? (
                  <AvGroup>
                    <Label for="id">ID</Label>
                    <AvInput id="ps-entity-id" type="text" className="form-control" name="id" required readOnly />
                  </AvGroup>
                ) : null}
                <AvGroup>
                  <Label id="nameLabel" for="name">
                    Name
                  </Label>
                  <AvField
                    id="ps-entity-name"
                    type="text"
                    name="name"
                    validate={{
                      required: { value: true, errorMessage: 'This field is required.' }
                    }}
                  />
                </AvGroup>
                <AvGroup>
                  <Label for="psentitytemplateid.id">Psentitytemplateid</Label>
                  <AvInput
                    id="ps-entity-psentitytemplateid"
                    type="select"
                    className="form-control"
                    name="psentitytemplateid.id"
                    onChange={this.psentitytemplateidUpdate}
                  >
                    <option value="" key="0" />
                    {pSEntityTemplates
                      ? pSEntityTemplates.map(otherEntity => (
                          <option value={otherEntity.id} key={otherEntity.id}>
                            {otherEntity.id}
                          </option>
                        ))
                      : null}
                  </AvInput>
                </AvGroup>
                <Button tag={Link} id="cancel-save" to="/entity/ps-entity" replace color="info">
                  <FontAwesomeIcon icon="arrow-left" />&nbsp;
                  <span className="d-none d-md-inline">Back</span>
                </Button>
                &nbsp;
                <Button color="primary" id="save-entity" type="submit" disabled={updating}>
                  <FontAwesomeIcon icon="save" />&nbsp; Save
                </Button>
              </AvForm>
            )}
          </Col>
        </Row>
      </div>
    );
  }
}

const mapStateToProps = (storeState: IRootState) => ({
  pSEntityTemplates: storeState.pSEntityTemplate.entities,
  pSEntityEntity: storeState.pSEntity.entity,
  loading: storeState.pSEntity.loading,
  updating: storeState.pSEntity.updating
});

const mapDispatchToProps = {
  getPSEntityTemplates,
  getEntity,
  updateEntity,
  createEntity,
  reset
};

type StateProps = ReturnType<typeof mapStateToProps>;
type DispatchProps = typeof mapDispatchToProps;

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PSEntityUpdate);
