import axios from 'axios';
import { ICrudGetAction, ICrudGetAllAction, ICrudPutAction, ICrudDeleteAction } from 'react-jhipster';

import { cleanEntity } from 'app/shared/util/entity-utils';
import { REQUEST, SUCCESS, FAILURE } from 'app/shared/reducers/action-type.util';

import { IPSCustomFieldsTemplate, defaultValue } from 'app/shared/model/ps-custom-fields-template.model';

export const ACTION_TYPES = {
  FETCH_PSCUSTOMFIELDSTEMPLATE_LIST: 'pSCustomFieldsTemplate/FETCH_PSCUSTOMFIELDSTEMPLATE_LIST',
  FETCH_PSCUSTOMFIELDSTEMPLATE: 'pSCustomFieldsTemplate/FETCH_PSCUSTOMFIELDSTEMPLATE',
  CREATE_PSCUSTOMFIELDSTEMPLATE: 'pSCustomFieldsTemplate/CREATE_PSCUSTOMFIELDSTEMPLATE',
  UPDATE_PSCUSTOMFIELDSTEMPLATE: 'pSCustomFieldsTemplate/UPDATE_PSCUSTOMFIELDSTEMPLATE',
  DELETE_PSCUSTOMFIELDSTEMPLATE: 'pSCustomFieldsTemplate/DELETE_PSCUSTOMFIELDSTEMPLATE',
  RESET: 'pSCustomFieldsTemplate/RESET'
};

const initialState = {
  loading: false,
  errorMessage: null,
  entities: [] as ReadonlyArray<IPSCustomFieldsTemplate>,
  entity: defaultValue,
  updating: false,
  updateSuccess: false
};

export type PSCustomFieldsTemplateState = Readonly<typeof initialState>;

// Reducer

export default (state: PSCustomFieldsTemplateState = initialState, action): PSCustomFieldsTemplateState => {
  switch (action.type) {
    case REQUEST(ACTION_TYPES.FETCH_PSCUSTOMFIELDSTEMPLATE_LIST):
    case REQUEST(ACTION_TYPES.FETCH_PSCUSTOMFIELDSTEMPLATE):
      return {
        ...state,
        errorMessage: null,
        updateSuccess: false,
        loading: true
      };
    case REQUEST(ACTION_TYPES.CREATE_PSCUSTOMFIELDSTEMPLATE):
    case REQUEST(ACTION_TYPES.UPDATE_PSCUSTOMFIELDSTEMPLATE):
    case REQUEST(ACTION_TYPES.DELETE_PSCUSTOMFIELDSTEMPLATE):
      return {
        ...state,
        errorMessage: null,
        updateSuccess: false,
        updating: true
      };
    case FAILURE(ACTION_TYPES.FETCH_PSCUSTOMFIELDSTEMPLATE_LIST):
    case FAILURE(ACTION_TYPES.FETCH_PSCUSTOMFIELDSTEMPLATE):
    case FAILURE(ACTION_TYPES.CREATE_PSCUSTOMFIELDSTEMPLATE):
    case FAILURE(ACTION_TYPES.UPDATE_PSCUSTOMFIELDSTEMPLATE):
    case FAILURE(ACTION_TYPES.DELETE_PSCUSTOMFIELDSTEMPLATE):
      return {
        ...state,
        loading: false,
        updating: false,
        updateSuccess: false,
        errorMessage: action.payload
      };
    case SUCCESS(ACTION_TYPES.FETCH_PSCUSTOMFIELDSTEMPLATE_LIST):
      return {
        ...state,
        loading: false,
        entities: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.FETCH_PSCUSTOMFIELDSTEMPLATE):
      return {
        ...state,
        loading: false,
        entity: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.CREATE_PSCUSTOMFIELDSTEMPLATE):
    case SUCCESS(ACTION_TYPES.UPDATE_PSCUSTOMFIELDSTEMPLATE):
      return {
        ...state,
        updating: false,
        updateSuccess: true,
        entity: action.payload.data
      };
    case SUCCESS(ACTION_TYPES.DELETE_PSCUSTOMFIELDSTEMPLATE):
      return {
        ...state,
        updating: false,
        updateSuccess: true,
        entity: {}
      };
    case ACTION_TYPES.RESET:
      return {
        ...initialState
      };
    default:
      return state;
  }
};

const apiUrl = 'api/ps-custom-fields-templates';

// Actions

export const getEntities: ICrudGetAllAction<IPSCustomFieldsTemplate> = (page, size, sort) => ({
  type: ACTION_TYPES.FETCH_PSCUSTOMFIELDSTEMPLATE_LIST,
  payload: axios.get<IPSCustomFieldsTemplate>(`${apiUrl}?cacheBuster=${new Date().getTime()}`)
});

export const getEntity: ICrudGetAction<IPSCustomFieldsTemplate> = id => {
  const requestUrl = `${apiUrl}/${id}`;
  return {
    type: ACTION_TYPES.FETCH_PSCUSTOMFIELDSTEMPLATE,
    payload: axios.get<IPSCustomFieldsTemplate>(requestUrl)
  };
};

export const createEntity: ICrudPutAction<IPSCustomFieldsTemplate> = entity => async dispatch => {
  const result = await dispatch({
    type: ACTION_TYPES.CREATE_PSCUSTOMFIELDSTEMPLATE,
    payload: axios.post(apiUrl, cleanEntity(entity))
  });
  dispatch(getEntities());
  return result;
};

export const updateEntity: ICrudPutAction<IPSCustomFieldsTemplate> = entity => async dispatch => {
  const result = await dispatch({
    type: ACTION_TYPES.UPDATE_PSCUSTOMFIELDSTEMPLATE,
    payload: axios.put(apiUrl, cleanEntity(entity))
  });
  dispatch(getEntities());
  return result;
};

export const deleteEntity: ICrudDeleteAction<IPSCustomFieldsTemplate> = id => async dispatch => {
  const requestUrl = `${apiUrl}/${id}`;
  const result = await dispatch({
    type: ACTION_TYPES.DELETE_PSCUSTOMFIELDSTEMPLATE,
    payload: axios.delete(requestUrl)
  });
  dispatch(getEntities());
  return result;
};

export const reset = () => ({
  type: ACTION_TYPES.RESET
});
